import React, {FC, LegacyRef, RefObject, useEffect, useRef} from "react";
import Message from "../../message/message";
import s from './chatMessages.module.css'
import {MessageType} from "../../../redux/reducers/chatReducer";


const ChatMessages: FC<{ messages: MessageType[] }> = ({messages}) => {
    const refScroll:LegacyRef<HTMLDivElement> | undefined = React.createRef()

    useEffect(() =>{
        refScroll.current?.scrollIntoView()
    }, [messages])
    return (
        <div className={s.chatMessages}>
            {
                messages.map(m => {
                    return (
                        <Message className={s.message}  userId={m.userId}
                                 name={m.userName} message={m.message} avatar={m.photo}/>
                    )
                })

            }
            <div ref={refScroll} />
        </div>
    )
}

export default ChatMessages