import React, {FC} from 'react';
import s from './logon.module.css';
import button from '../../../commonStyle/button.module.css'
import avatar from '../../../assests/image/avatar.jpg'

type PropsType = {
    userLogin:string | null
    logout: () => void
    smallPhotos: string | null | undefined
}
const Logon:FC<PropsType> = (props) =>{
    return(
        <div className={s.logon}>

            <div className={s.userLogin}>
                <h3>{props.userLogin}</h3>
            </div>
            <div className={s.avatar}>
                <img alt='avatar' src={props.smallPhotos || avatar} />
            </div>
            <div className={s.menu}>
                <button className={button.button} onClick={props.logout}>logout</button>
            </div>

        </div>
    );
}

export default Logon;