import React, {FC} from 'react';
import s from './logout.module.css';
import button from '../../../commonStyle/button.module.css'
import {NavLink} from "react-router-dom";

type PropsType = {

}
const Logout:FC<PropsType> = (props) => {
    return (
        <div className={s.logout}>
            <NavLink to='/login'>
                <button className={`${s.login} ${button.button}`}>Login</button>
            </NavLink>
            <button className={`${s.registration} ${button.button}`}>Registration</button>
        </div>
    );
}

export default Logout;