import React, {FC} from "react";
import s from "./itemList.module.css";
type InfoListPropsType = {
    label:string
    className?:string
}
const ItemList:FC<InfoListPropsType> = ({className = '',children, label}) =>{
    return(
        <div  className={`${s.itemList} ${className}`}>
            <span>{label}</span>
            <div className={s.items}>
                {children}
            </div>
        </div>
    )
}
export default ItemList