import React, {FC} from 'react';
import s from './message.module.css';
import {useSelector} from "react-redux";
import {getMyUserId} from "../../redux/selectors/authSelectors";
import {NavLink} from 'react-router-dom';

type PropsType = {
    className?: string
    avatar?: string
    userId: number
    name: string
    message: string
}
const Message: FC<PropsType> = (props) => {
    const myId = useSelector(getMyUserId)
    return (
        <div className={`${s.main} ${(props.className || '')} ${props.userId === myId ? s.me : s.notMe}`}>
            <div className={s.avatar}>
                <NavLink to={'profile/' + props.userId}>
                    <img alt='avatar'
                         src={props.avatar || 'https://pixelbox.ru/wp-content/uploads/2020/12/avatar-youtube-99.jpg'}/>
                </NavLink>
            </div>
            <div className={s.messageInfo}>
                <div className={s.name}>
                    <h4>{props.name}</h4>
                </div>
                <div className={`${s.message} ${s.newMessage}`}>
                    <p>{props.message}</p>
                </div>
            </div>
        </div>
    );
}

export default Message;