import React from 'react';
import s from './formField.module.css';
import {ErrorMessage, Field} from "formik";

type PropsType = {
    name: string
    error: string | undefined
    labelText?: string
    addComponent?: React.ReactNode
    inputTeg?: string
    type?: string
    placeholder?: string
    innerField?: Array<React.ReactNode> | null
}
const FormField = ({
                       name, error, labelText = name, addComponent = null,
                       inputTeg = 'input', type = 'text', placeholder = name, innerField = null
                   }: PropsType) => {
    return (
        <div className={s.formField}>
            <label className={s.label} htmlFor={name}>{labelText}</label>
            {addComponent}
            <div className={s.content}>
                <Field className={`${s[inputTeg]} ${s.field}`} as={inputTeg} id={name} type={type} name={name}
                       placeholder={placeholder}>
                    {innerField?.map(el => el)}
                </Field>
                {error ? <div className={s.errorContent}>
                    <div className={s.errorIcon}>!</div>
                    <span className={s.errorMessage}>
                        {error}
                    </span>
                </div>
                    : null}


            </div>

        </div>
    );
}

export default FormField;