import {
    MessageType,
    startListeningMessages,
    sendMessage,
    stopListeningMessages
} from "../../redux/reducers/chatReducer";
import {connect} from "react-redux";
import Chat from "./chat";
import {StateType} from "../../redux/reduxStore";
import {getChatMessages, getConnectStatus} from "../../redux/selectors/chatSelectors";
import {ConnectStatusType} from "../../api/chatAPI";

type MapPropsType = {
    messages:Array<MessageType>
    status:ConnectStatusType
}
type MapDispatchType = {
    startListeningMessages: () => void
    stopListeningMessages:() => void
    sendMessage: (message:string) => void
}
export type ChatPropsType = MapPropsType & MapDispatchType
const MapStateToProps = (state:StateType):MapPropsType =>({
    messages:getChatMessages(state),
    status:getConnectStatus(state)
})


const ChatContainer = connect<MapPropsType, MapDispatchType, {}, StateType>(MapStateToProps,
    { startListeningMessages, stopListeningMessages, sendMessage})(Chat)
export default ChatContainer