import React, {ChangeEvent, FC} from "react";
import s from './textarea.module.css'

type TextareaPropsType = {
    value: string
    className?: string
    onChange?: (e: ChangeEvent<HTMLTextAreaElement>) => void
    onEnter?: () => void
}
const AutoSizeTextarea: FC<TextareaPropsType> = ({value, onChange, onEnter, className}) => {
    const onChangeIn = (e: ChangeEvent<HTMLTextAreaElement>) => {
        e.currentTarget.style.height = 'auto';
        e.currentTarget.style.height = e.currentTarget.scrollHeight + 'px';
        if (onChange) {
            onChange(e)
        }
    }
    const onKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
        if (e.key === 'Enter') {
            if (!e.shiftKey && onEnter) {
                onEnter()
                e.preventDefault()
            }
        }
    }
    return (
        <textarea onKeyDown={onKeyDown} className={`${s.textarea} ${className || ''}`} onChange={onChangeIn}
                  value={value}/>
    )
}

export default AutoSizeTextarea