import {ChangeEvent, FC, useState} from "react";
import AutoSizeTextarea from "../../common/textarea/textarea";
import {ConnectStatusType} from "../../../api/chatAPI";
import button from '../../../commonStyle/button.module.css'
import s from './sendMessage.module.css'

type SendMessageFormType = {
    sendMessage: (message: string) => void
    status: ConnectStatusType
}
const SendMessage: FC<SendMessageFormType> = ({sendMessage, status}) => {
    const [message, setMessage] = useState('')
    const onChange = (e:ChangeEvent<HTMLTextAreaElement>) =>{
        setMessage(e.currentTarget.value)
    }
    const sendMessageIn = () =>{
        setMessage('')
        sendMessage(message)
    }
    return (
        <div className={s.main}>
            <AutoSizeTextarea className={s.textarea} onChange={onChange} onEnter={sendMessageIn} value={message}/>
            <button className={button.button} disabled={status !== "connected"} onClick={sendMessageIn}>Send</button>
        </div>
    )
}
export default SendMessage