import React from 'react';
import profileIcon from '../../assests/icons/profileIcon.svg'
import messagesIcon from '../../assests/icons/messagesIcon.svg'
import usersIcon from '../../assests/icons/usersIcon.svg'
import s from './navbar.module.css';
import {NavLink} from "react-router-dom";

const Navbar = ()=>{
    return(
      <nav className={s.navbar}>
          <div className={s.item}>
              <img src={profileIcon} />
              <NavLink activeClassName={s.active} to='/profile'>Profile</NavLink>
          </div>
          <div className={s.item}>
              <img src={messagesIcon} />
              <NavLink activeClassName={s.active} to='/dialogs'>Messages</NavLink>
          </div>
          <div className={s.item}>
              <img src={usersIcon} />
              <NavLink activeClassName={s.active} to='/users'>Users</NavLink>
          </div>
          <div className={s.item}>
              <img src={messagesIcon} />
              <NavLink activeClassName={s.active} to='/chat'>Chat</NavLink>
          </div>

      </nav>
    );
}

export default Navbar;