import React, {ChangeEvent, FC, useEffect, useState} from "react";
import ChatMessages from "./chatMessages/chatMessages";
import {ChatPropsType} from "./chatContainer";
import AutoSizeTextarea from "../common/textarea/textarea";
import SendMessageForm from "./sendMessage/sendMessage";


const Chat:FC<ChatPropsType> = ({messages, startListeningMessages, sendMessage, status,stopListeningMessages}) =>{

    useEffect(() =>{
        startListeningMessages()
        return stopListeningMessages
    }, [])
    return(
        <div>
            {status}
            <ChatMessages messages={messages} />
            <SendMessageForm  sendMessage={sendMessage} status={status} />
        </div>
    )
}
export default Chat