import {InferActionsTypes, StateType} from "../reduxStore";
import {ThunkAction} from "redux-thunk";
import {chatAPI, ConnectStatusType} from "../../api/chatAPI";

const SET_MESSAGE = 'social/chat/SET-MESSAGE', STATUS_CHANGE = 'social/chat/STATUS-CHANGE', DELETE_MESSAGES='social/chat/DELETE-MESSAGES';
export type MessageType = {
    message: string
    photo: string
    userId: number
    userName: string
}
const initState = {
    messages: [] as Array<MessageType>,
    status:'disconnect' as ConnectStatusType
}
type InitStateType = typeof initState
type ActionsTypes = InferActionsTypes<typeof actions>
type ThunkType<T> = ThunkAction<T, StateType, unknown, ActionsTypes>
const chatReducer = (state = initState, action: ActionsTypes): InitStateType => {
    switch (action.type) {
        case SET_MESSAGE:
            return {
                ...state,
                messages: [...state.messages, ...action.messages]
            }
        case STATUS_CHANGE:
            return{
                ...state,
                status: action.status
            }
        case DELETE_MESSAGES:
            return{
                ...state,
                messages: []
            }
        default:
            return state;
    }
}
export const actions = {
    setMessages: (messages:Array<MessageType>) => ({type: SET_MESSAGE, messages} as const),
    statusChange: (status:ConnectStatusType) => ({type: STATUS_CHANGE, status} as const),
    deleteMessages:() =>({type:DELETE_MESSAGES} as const)
}


export const startListeningMessages = (): ThunkType<void> => async (dispatch) => {
    chatAPI.subscribeEvent("statusChanged", (status:ConnectStatusType) =>{
        dispatch(actions.statusChange(status))
    })
    chatAPI.start()
    chatAPI.subscribeEvent("messagesReceived", (messages:Array<MessageType>) =>{
        dispatch(actions.setMessages(messages))
    })

}
export const stopListeningMessages = (): ThunkType<void> => async (dispatch) => {
    chatAPI.stop()
    debugger;
    dispatch(actions.deleteMessages())
}
export const sendMessage = (message:string): ThunkType<void> => async (dispatch) =>{
    chatAPI.sendMessage(message)
}

export default chatReducer;