import React, {ChangeEvent, FC} from 'react';
import s from './avatar.module.css';
import avatar from '../../../../assests/image/avatar.jpg';

type PropsType = {
    photo: string | null | undefined
    isOwner: boolean
    saveImage: (file: File) => void
}
const Avatar: FC<PropsType> = (props) => {
    const fileIsSelected = (e: ChangeEvent<HTMLInputElement>) => {
        const files = e.currentTarget.files
        if (files !== null && files[0]) {
            props.saveImage(files[0])
        }
    }
    return (
        <div className={s.avatar}>
            <img alt=''
                 src={props.photo || avatar}/>

            {props.isOwner ? <AvatarSettings fileIsSelected={fileIsSelected}/> : null}
        </div>
    );
}
type AvatarSettingsPropsType = {
    fileIsSelected: (e: ChangeEvent<HTMLInputElement>) => void
}
const AvatarSettings: FC<AvatarSettingsPropsType> = ({fileIsSelected}) => {
    return (
        <div className={s.avatarSettings}>
            <div className={s.uploadFile}>
                <label htmlFor='file'/>
                <input id='file' onChange={fileIsSelected} type='file'/>
            </div>
        </div>
    )
}

export default Avatar;