import {getProfileData} from "./authReducer";
import {InferActionsTypes, StateType} from "../reduxStore";
import {ThunkAction} from "redux-thunk";
import {requestProfileInformation} from "./profileReducer";

const INITIALIZE = 'social/app/INITIALIZE';

const initState = {
    initialized: false
}
type InitStateType = typeof initState
type ActionsTypes = InferActionsTypes<typeof actions>
type ThunkType<T> = ThunkAction<T, StateType, unknown, ActionsTypes>
const appReducer = (state = initState, action: ActionsTypes): InitStateType => {
    switch (action.type) {
        case INITIALIZE:
            return {
                ...state,
                initialized: true
            }
        default:
            return state;
    }
}
export const actions = {
    initializedSuccess: () => ({type: INITIALIZE} as const)
}


export const initialize = (): ThunkType<void> => async (dispatch) => {
    await dispatch(getProfileData()).then((data: any) => {
        if (data.resultCode === 0) {
            return dispatch(requestProfileInformation(data.data.id, true))
        }
    })
    dispatch(actions.initializedSuccess());
}

export default appReducer;