import React, {FC} from 'react';
import s from './profileData.module.css';
import button from '../../../../commonStyle/button.module.css'
import {ContactsType, ProfileType} from "../../../../redux/reducers/profileReducer";
import ItemList from "../../../common/itemList/itemList";

type PropsType = {
    profile: ProfileType
    isOwner: boolean
    onEdit: () => void
}
type InfoItemPropsType = {
    itemName:string
    itemValue:string | null
    isLink?:boolean
}

const ProfileData: FC<PropsType> = ({profile, onEdit, isOwner}) => {
    return (
        <div className={s.profileData}>
            <ItemList label='Personal information'>
                <InfoItem itemName='Full name' itemValue={profile.fullName}/>
                <InfoItem itemName='About me' itemValue={profile.aboutMe}/>
                <InfoItem itemName='Skills' itemValue={profile.lookingForAJobDescription}/>
            </ItemList>
            <ItemList label='Contacts'>
                {Object.keys(profile.contacts).map(keyName => {
                    return <InfoItem isLink={true} itemName={keyName} itemValue={profile.contacts[keyName as keyof ContactsType]}/>
                })}
            </ItemList>
            {
                isOwner ? <button className={button.button} onClick={onEdit}>Edit Information</button> : null
            }
        </div>
    );
}

const InfoItem:FC<InfoItemPropsType> = ({itemName, itemValue, isLink = false}) =>{

    return(
        <div className={s.infoItem}>
            <span>{`${itemName}:` }</span>
            {isLink? <a rel='noreferrer' href={itemValue as string} target='_blank'> {itemValue} </a> : <p>{itemValue}</p>}
        </div>
    )
}
export default ProfileData;