import {MessageType} from "../redux/reducers/chatReducer";

const URL = 'wss://social-network.samuraijs.com/handlers/ChatHandler.ashx'

export type ConnectStatusType = 'connected' | 'connecting' | 'error' | 'disconnect'
type MessagesReceivedType = (messages: Array<MessageType>) => void
type StatusChangedType = (status: ConnectStatusType) => void
type SubscribesType = {
    messagesReceived: Array<MessagesReceivedType>
    statusChanged: Array<StatusChangedType>
}
type EventType = keyof SubscribesType


const subscribes: SubscribesType = {
    statusChanged: [],
    messagesReceived: []
}

let ws: WebSocket | null = null
let reconnect: NodeJS.Timeout | null = null;
const changeConnectStatus = (status: ConnectStatusType) => {
    subscribes.statusChanged.forEach(h => h(status))
}
const messageHandler = (e: MessageEvent) => {
    const messages = JSON.parse(e.data)
    subscribes.messagesReceived.forEach(h => h(messages))
}
const closeHandler = (e: CloseEvent) => {
    changeConnectStatus('disconnect')
    setTimeout(chatAPI.start, 3000)
}
const openHandler = () => {
    changeConnectStatus('connected')
}
const errorHandler = () => {
    changeConnectStatus('error')
}
const cleanUp = () => {
    if (ws) {
        ws.removeEventListener("message", messageHandler)
        ws.removeEventListener("close", closeHandler)
        ws.removeEventListener("open", openHandler)
    }
}

export const chatAPI = {
    stop() {
        subscribes.statusChanged = []
        subscribes.messagesReceived = []
        cleanUp()
        ws?.close()
        ws = null
    },
    start() {
        changeConnectStatus('connecting')
        cleanUp()
        ws = new WebSocket(URL)
        ws.addEventListener("message", messageHandler)
        ws.addEventListener("open", openHandler)
        ws.addEventListener("close", closeHandler)
        ws.addEventListener("error", errorHandler)

    },
    subscribeEvent(Event: EventType, callback: MessagesReceivedType | StatusChangedType) {
        // @ts-ignore
        subscribes[Event].push(callback)
    },
    unSubscribeEvent(Event: EventType, callback: MessagesReceivedType | StatusChangedType) {
        // @ts-ignore
        subscribes[Event].splice(subscribes[Event].indexOf(callback), 1)
    },
    sendMessage(message: string) {
        ws?.send(message)
    }

}
