import React, {FC} from 'react';
import s from './pagination.module.css';


type OnPageNumberClickType = (e: React.MouseEvent<HTMLDivElement>) => void
type PropsType = {
    totalPageCount: number
    currentPage: number

    setCurrentPage: (pageNumber:number) => void
    getItems: (pageNumber:number, term:string, friend:boolean | null) => void
}
type PageNumberItemPropsType = {
    onClick?:OnPageNumberClickType
    appendClassName?:string
    value:any
}
const PageNumberItem:FC<PageNumberItemPropsType> = ({onClick, value,appendClassName = ''}) =>{
    return (
        <div onClick={onClick} className={`${s.pageNumber} ${appendClassName}`}>{value}</div>
    )
}
const getPageNumbers = (pageCount:number, currentPage:number, onPageNumberClick:OnPageNumberClickType)=>{
    let pageNumbers = [];
    if(currentPage > 6){
        for(let i = 1; i < 4; i++){
            pageNumbers.push(<PageNumberItem onClick={onPageNumberClick} value={i} />);
        }
        pageNumbers.push(<PageNumberItem value='...' />);
    }

    if (currentPage + 4 < pageCount) {
        let startIndex;
        if(currentPage !== 1){
            startIndex = currentPage - 1;
        }else{
            startIndex = currentPage;
        }
        for (let i = startIndex; i < startIndex + 3; i++) {
            if(i === currentPage){
                pageNumbers.push(<PageNumberItem value={i} onClick={onPageNumberClick} appendClassName={s.active} />);
            }else{
                pageNumbers.push(<PageNumberItem value={i} onClick={onPageNumberClick} />)
            }
        }
        pageNumbers.push(<PageNumberItem value='...' />)
        for (let i = pageCount - 3; i <= pageCount; i++) {
            pageNumbers.push(<PageNumberItem value={i} onClick={onPageNumberClick} />)
        }
    } else {
        for (let i = pageCount - 5; i <= pageCount; i++) {
            if(i === currentPage){
                pageNumbers.push(<PageNumberItem value={i} onClick={onPageNumberClick} appendClassName={s.active}/>);
            }else{
                pageNumbers.push(<PageNumberItem value={i} onClick={onPageNumberClick} />)

            }
        }
    }

    return pageNumbers;
}
const Pagination: FC<PropsType> = React.memo(function Pagination(props){
    const onPageNumberClick = (e: React.MouseEvent<HTMLDivElement>) =>{
        const pageNumber = parseInt(e.currentTarget.innerHTML);
        props.setCurrentPage(pageNumber);
        props.getItems(pageNumber, '', null);
    }
    const pageNumbers = getPageNumbers(props.totalPageCount, props.currentPage, onPageNumberClick);
    return (
        <div className={s.pagination}>
            <div className={s.pageNumbers}>
                {pageNumbers}
            </div>
        </div>
    );
})

export default Pagination;